.information-city__headline {
  margin-top: 43px;
  line-height: 26px;
  @include font(18px, 700, black, center);
  text-transform: uppercase;
}

.information-city__text {
  @include font(14px, 400, #333333, center);
  line-height: 26px;
  text-transform: uppercase;
}

.information-city__wrapper {
  background-color: white;
  width: 320px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: content-box;
  overflow: hidden;
}
